* {
  margin: 0;
  padding: 0;
}

body {
  background: linear-gradient(rgb(112, 206, 238), rgb(226, 240, 240)) fixed;
}

.App {
  margin: 2rem auto;
  max-width: 500px;
  background-color: rgba(0, 0, 0, 0.1);
  padding: 1rem;
  font-family: Calibri, sans-serif;
  border-radius: 2rem;
}

.Todo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  margin: 1rem;
  padding: 1rem;
  background-color: whitesmoke;
  border-radius: 1rem;
}

.Header {
  text-align: center;
}

.headerText {
  color: rgba(0, 0, 0, 0.7);
}

.addTodoButton {
  font-size: 2rem;
  color: rgba(0, 0, 0, 0.7);
}

.deleteTodoButton {
  font-size: 1.5rem;
}

.addTodoButton,
.deleteTodoButton {
  cursor: pointer;
}
.deleteTodoButton {
  color: rgb(121, 121, 121);
}
.deleteTodoButton:hover {
  color: rgb(32, 32, 32);
}
.addTodoButton:hover {
  color: white;
}

.AddForm {
  display: flex;
  justify-content: space-evenly;
  margin: 0 1rem;
}

.addFormInput {
  width: 80%;
}

input {
  accent-color: rgb(28, 134, 255);
}

input[type="text"] {
  border-radius: 0.2rem;
  border: none;
  padding: 0.5rem;
}

input:focus {
  outline: none;
}

input[type="checkbox"] {
  transform: scale(1.5, 1.5);
}

.grayedOut {
  background-color: rgb(211, 211, 211);
}
